.card {
    background: #fff;
    border-radius: 2px;
    height: 200px;
    margin: 1rem;
    position: relative;
    width: 280px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
    cursor: pointer;
    -webkit-transition: -webkit-transform .2s;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
  }
  
  .card > .img-container {
    height: 100%;
    overflow: hidden;
    text-align: center;
  }
  .card > .img-container > img {
    width: 280px;
    height: 220px;
  }
  
  .card > .content {
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 15px;
  }

  .card :hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }


  
.wrapper {
    padding-top: 50px;
    background-color: white;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    padding: 20px;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
  }

.nav {
  width: 100%;
  font-size: 2rem;
  color: whitesmoke;
  margin-bottom: 10px;
  background-color: #2A77AE
}
  
.title {
  float: left;
  cursor: pointer;
  margin-right: 150px;
}
  
.score {
  float: right;
  margin-left: 150px;
}

  

.jumbotron {
    background-image: url("https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcS7sMT-vPRp34vWlrJpXV7oSZYdIDeiS5oKMwKBsoq1yNAE-94d");
    width: 100%;
    height: 250px;
    background-repeat: no-repeat;
    background-size: contain;
    background-color: white;
    background-position: center;
}

h1, h2 {
    text-align: center;
    font-weight: bold;
    color: lightblue;
}

h1 {
    font-size: 5em
}
* {
    box-sizing: border-box;
    font-family: sans-serif;
  }
  
  html, body, #root {
    padding: 0;
    margin: 0;
    height: 100%;
  }
